<template>
  <div class="UserMgr">
    <div class="UserMgr-content">
      <div class="header-operation">
        <el-upload
          action=""
          :show-file-list="false"
          :before-upload="beforeUpload">
            <el-button type="primary" size="mini">本地上传</el-button>
        </el-upload>
        <div class="btn-line">
          <el-button v-for="(item,index) in ratioArr" :key="index" :disabled="hasImg"
            :class="{active:item.isActive}" class="ratio-btn"
            size="mini" @click="changeRatio(item.ratio,index)">{{item.ratio}}
          </el-button>
          <el-button :disabled="hasImg" @click="everyRatio"
            :class="{active:isEreryRatio}" size="mini">任意比例
          </el-button>
        </div>
      </div>
      <div class="cropper">
        <VueCropper
          v-bind="option"
          ref="VueCropper"
          :img="option.img"
        ></VueCropper>
      </div>
      <div class="footer">
        <!-- 原始图片 -->
        <div v-show="false">
          <img id="originalImg" src="originalImg" alt="原始图片">
        </div>
        <div class="footer-operation">
          <el-button type="primary" size="mini" :disabled="hasImg" @click="rotateLeft">
            <em class="fa fa-reply"></em>左旋转
          </el-button>
          <el-button type="primary" size="mini" :disabled="hasImg" @click="rotateRight">
            <em class="fa fa-share"></em>右旋转
          </el-button>
          <el-button type="primary" size="mini" :disabled="hasImg" @click="handleCutImg">裁剪</el-button>
          <el-button type="primary" size="mini" :disabled="hasImg" @click="downloadIamge">下载</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { VueCropper }  from 'vue-cropper'
// import {getFileSize} from "@/utils/js/file.js"
export default {
  name:"Picture",
  components:{
    VueCropper
  },
  data(){
    return{
       option: {
        img: "", // 裁剪图片的地址
        info: true, // 裁剪框的大小信息
        outputSize: 1, // 裁剪生成图片的质量
        outputType: "jpeg", // 裁剪生成图片的格式
        canScale: false, // 图片是否允许滚轮缩放
        canMove: false, // 上传图片是否可以移动
        autoCrop: true, // 是否默认生成截图框
        autoCropWidth: 0, // 默认生成截图框宽度
        autoCropHeight: 0, // 默认生成截图框高度
        fixedBox: false, // 固定截图框大小 不允许改变
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [1, 1], // 截图框的宽高比例       (按钮修改)
        original: false, // 上传图片按照原始比例渲染
        full: true, // 是否输出原图比例的截图
        canMoveBox: true, // 截图框能否拖动
        centerBox: true, // 截图框是否被限制在图片里面
        enlarge: 1, // 图片根据截图框输出比例倍数
        maxImgSize: 10000,
        mode: "contain",
        infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
      },
      uploadInfo:{
        imgUrl:"",
        fileSize:"",
        name:"",
        format:"" //图片格式
      },
      ratioArr:[
        {ratio:"1:1",isActive:true},
        {ratio:"2:3",isActive:false},
        {ratio:"3:2",isActive:false},
        {ratio:"3:4",isActive:false},
        {ratio:"4:3",isActive:false},
        {ratio:"9:16",isActive:false},
        {ratio:"16:9",isActive:false}
      ],
      originalImg:"",
      isEreryRatio:false
    }
  },
  computed:{
    hasImg(){
       if(this.option.img === ""){
         return true
       }
       return false
    }
  },
  methods:{
    beforeUpload(file){
      let reg = /\.(jpg|jpeg|png|bmp|JPG|PNG|webp)$/
      if(!reg.test(file.name)){
        this.$message.error("请上传图片!")
        return
      }
      //图片大小最大10M
      if(file.size >10 *1024 *1024){
        this.$message.error("请选择小于10m的图片")
        return
      }
      const windowUrl = window.URL || window.webkitURL
      this.option.img = windowUrl.createObjectURL(file)
      this.originalImg = this.option.img
      //图片信息
      // this.uploadInfo.imgUrl = windowUrl.createObjectURL(file)
      // this.uploadInfo.fileSize = getFileSize(file.size)
      const lastIndex = file.name.lastIndexOf(".")
      this.uploadInfo.name = file.name.substring(0,lastIndex).trim()
      // this.uploadInfo.format = file.name.substring(lastIndex + 1).trim()
      //也可用正则的方式获取name,format
      // this.uploadInfo.fromat = file.name.match(/\.\S+$/g)[0].replace('.', '')
      // this.uploadInfo.name = file.name.match(/(\S*)\./)[1]
    },
    //切换比例
    changeRatio(ratio,index){
      //切换颜色
      this.ratioArr.forEach(i=>{
        i.isActive = false
      })
      this.isEreryRatio = false
      this.ratioArr[index].isActive = true
      //改变切图比例
      this.initRatio(ratio)
    },
    initRatio(ratio){
      let m = ratio.split(":").map(j=>Number(j))[0]
      let n = ratio.split(":").map(j=>Number(j))[1]
      let originalImg = document.getElementById("originalImg")
      let imgW =  Math.floor(originalImg.naturalWidth); // 原始图片的宽度
      let imgH = Math.floor(originalImg.naturalHeight); // 原始图片的高度
      this.cropRatio = m / n;
      if (m >= n) {
        // 截取横屏图片
        if (imgW / imgH >= this.cropRatio) {
          // 图片宽高比大于目标比例，以高度为准截取
          this.option.autoCropWidth = Math.floor((imgH * m) / n);
          this.option.autoCropHeight = Math.floor(imgH);
        } else {
          this.option.autoCropWidth = Math.floor(imgW);
          this.option.autoCropHeight = Math.floor((imgW * n) / m);
        }
      } else {
        // 截取竖屏图片
        if (imgW / imgH < this.cropRatio) {
          this.option.autoCropWidth = Math.floor(imgW);
          this.option.autoCropHeight = Math.floor((imgW * n) / m);
        } else {
          this.option.autoCropWidth = Math.floor((imgH * m) / n);
          this.option.autoCropHeight = Math.floor(imgH);
        }
      }
      //修改截图框比例
      this.option.fixed = false
      this.option.fixedNumber = [m,n]
      this.option.fixed = true

      //生成截图框
      let _this = this
       this.$nextTick(() => {
          _this.$refs.VueCropper.goAutoCrop(
          _this.option.autoCropWidth,
          _this.option.autoCropHeight
        );
      });
    },
    everyRatio(){
      this.ratioArr.forEach(i=>{
        i.isActive = false
      })
      this.isEreryRatio = true
      //任意比例
      this.option.fixed = false
    },
    //左旋转
    rotateLeft(){
      this.$refs.VueCropper.rotateLeft()
    },
    //右旋转
    rotateRight(){
      this.$refs.VueCropper.rotateRight()
    },
    //裁剪
    handleCutImg(){
      this.$refs.VueCropper.getCropBlob(blob => {
        const file = new File([blob], this.uploadInfo.name); // file对象
        let formData = new FormData()
        formData.append("file",file)
      })
      // console.log(11,this.$refs.VueCropper.cropW);
      // console.log(11,this.$refs.VueCropper.cropW.toFixed(2));
    },
    //下载
    downloadIamge(){
      this.$refs.VueCropper.getCropData(data => {
        //传入base64
        this.download(data)
      })
    },
    download(data){
      var a = document.createElement("a"); // 生成一个a元素
      var event = new MouseEvent("click"); // 创建一个单击事件
      a.download = this.uploadInfo.name; // 设置图片名称, 这里可以自定义，也可以获取图片名称进行修改
      a.href = data; // 将生成的base64设置为a.href属性
      a.dispatchEvent(event); // 触发a的单击事件
    }
  }
}
</script>

<style lang="less">
.UserMgr{
  .UserMgr-content{
    width: 100%;
    height: 100%;
    padding-left: 20px;
    padding-top: 5px;
    .header-operation{
      margin:30px 0 10px 0;
      display: flex;
      justify-content: start;
      .btn-line{
        margin-left: 50px;
        .active{
        background-color:#409EFF !important;
          color: #fff;
        }
        .ratio-btn{
          width: 60px;
        }
      }
    }
    .cropper{
      width: 720px;
      height: 360px;
      border: 2px dashed #ccc;
    }
    .footer-operation{
      margin-top:15px;
    }
  }
}
</style>



